.field_set {
	position: relative;

	.field_error {
		position: absolute;
		bottom: rem(17);
		left: rem(13);
		color: #EA374C;
		font-size: rem(10);
	}

	.field_error_icon {
		position: absolute;
		top: 50%;
		right: rem(10);
		margin-top: rem(-8);
		width: rem(16);
		height: rem(16);
		border-radius: 50%;
		background-color: #C84741;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "\e905";
			font-family: 'icons';
			color: white;
			font-size: rem(14);
		}
	}

	.field_success_icon {
		position: absolute;
		top: 50%;
		right: rem(10);
		margin-top: rem(-8);
		width: rem(16);
		height: rem(16);
		border-radius: 50%;
		background-color: #60C468;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "\e913";
			font-family: 'icons';
			color: white;
			font-size: rem(7);
		}
	}
}

.field_set {
	textarea.field~.field_success_icon,
	textarea.field~.field_error_icon {
		top: rem(18);
	}
}

.field {
	width: 100%;
	font-size: rem(14);
	color: #4E545A;
	background-color: white;
	padding: rem(11) rem(12);
	border-radius: rem(4);

	transition: all 300ms ease;

	border: rem(1) solid #E5E8EC;

	&:hover {
		border-color: #ADADAD;
	}

	&:active, &:focus {
		// border-color: #282B2E;
		border-color: #ADADAD;
	}

	@include placeholder {
		color: #ADADAD;
	}
}

textarea.field {
	resize: vertical;
}