.news {
	background-color: #F1F6FD;

	padding: rem(64) 0;

	&__top {
		margin-bottom: rem(24);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__bottom {
		margin: 0 rem(20);

		display: flex;
		flex-direction: column;

		h2 {
			margin-bottom: rem(8);
		}

		p {
			margin-bottom: rem(40);
		}

		.button_primary {
			// width: auto;
			align-self: center;
			height: auto;
			padding: rem(11) rem(40);
		}
	}
}