  align-items: center;
}

.header_burger__wrap {
  width: 1.25rem;
  height: 0.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header_burger__wrap .line, .header_burger__wrap::before, .header_burger__wrap::after {
  width: 100%;
  height: 0.125rem;
  border-radius: 0.0625rem;
  background-color: var(--c-dark);
  transition: all 200ms ease;
}

.header_burger__wrap::before, .header_burger__wrap::after {
  content: "";
}

.header_fixed .header__top {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 1.25rem;
  z-index: 10;
  background-color: #D5E4F3;
}

.header_menu_active .header_burger,
.header_menu_active .logo,
.header_menu_active .header_bay {
  position: relative;
  z-index: 11;
}

.header_menu_active .header_burger__wrap::before, .header_menu_active .header_burger__wrap::after {
  position: absolute;
  top: 0.875rem;
  left: 0;
}

.header_menu_active .header_burger__wrap::before, .header_menu_active .header_burger__wrap::after, .header_menu_active .header_burger__wrap .line {
  border-radius: 0;
}

.header_menu_active .header_burger__wrap::before {
  transform: rotate(45deg);
}

.header_menu_active .header_burger__wrap::after {
  transform: rotate(-45deg);
}

.header_menu_active .header_burger__wrap .line {
  opacity: 0;
}

.header_menu_active .header_menu__list {
  display: block;
  height: 100%;
  overflow-y: auto;
}

.header_menu_active_child .header_burger__wrap::before, .header_menu_active_child .header_burger__wrap::after {
  position: absolute;
  left: 0;
}

.header_menu_active_child .header_burger__wrap::before, .header_menu_active_child .header_burger__wrap::after,
.header_menu_active_child .header_burger__wrap .line {
  border-radius: 0.0625rem;
  width: 0.625rem;
}

.header_menu_active_child .header_burger__wrap::before {
  top: 0.625rem;
  left: 0.5rem;
  transform: rotate(-45deg);
}

.header_menu_active_child .header_burger__wrap::after {
  top: 1rem;
  left: 0.5rem;
  transform: rotate(45deg);
}

.header_menu_active_child .header_menu__child {
  display: block;
  height: 100%;
  overflow-y: auto;
}

.field_set {
  position: relative;
}

.field_set .field_error {
  position: absolute;
  bottom: 1.0625rem;
  left: 0.8125rem;
  color: #EA374C;
  font-size: 0.625rem;
}

.field_set .field_error_icon {
  position: absolute;
  top: 50%;
