#header_store {
	height: rem(181);
	background: url(../img/page_store/header_bg_x1.jpg) no-repeat;
	background-size: cover;

	> .container {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: rem(54);
			width: rem(300);
			height: rem(74);
			background: url(../img/page_store/header_i_x1.png) no-repeat top center;
		}
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(20);
		padding-bottom: 0;

		h1 {
			font-size: rem(22);
			font-weight: normal;
			margin-bottom: rem(20);
		}

	}
}