.facts {
	padding: rem(64) 0;

	h2 {
		text-align: center;
		margin-bottom: rem(32);
	}

	&__item {
		.image {
			display: none;
		}

		.info {

			&__text {

				p {
					position: relative;
					font-size: rem(20);
					line-height: rem(31);
					color: #4E545A;
					padding-left: rem(75);

					&::before {
						content: "\e917";
						position: absolute;
						top: rem(10);
						left: 0;
						font-family: 'icons';
						font-size: rem(42);
						color: #151522;
					}
				}
			}
		}
	}

	.swiper-nav {
		margin-top: rem(40);
		height: rem(20);

		button {
			i {
				&::before {
					color: #30384B;
				}
			}
		}
	}
}