:root {
	--c-accent: #3CA582;
	--c-dirty-blue: #E0E9F4;
	--c-light-blue: #F1F6FD;
	--c-dark: #282B2E;
	--c-paragraph: #4E545A;
}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;

	@media (min-width: 1200px) and (max-width: 1280px) {
		font-size: 92%;
	}
}

button {
	background-color: transparent;
	
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}



p {
	font-size: rem(13);
	line-height: 152%;
	letter-spacing: rem(-0.2);
}

body {
	position: relative;
	font-size: rem(13);
	min-width: 320px;
	font-family: $font-mplus;
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	width: calc(100% - 40px);
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button_primary {
	border-radius: rem(25);
	height: rem(56);
	padding: rem(18) rem(26);
	background-color: var(--c-accent);

	font-weight: 500;
	color: white;
	font-size: rem(14);

	transition: all 200ms ease;

	&:hover {
		background-color: $green_h;
	}

	&:active {
		box-shadow: inset 0px rem(1) 0px #2F886A,
		inset 0px rem(-1)0px #2F886A,
		inset rem(1) 0px 0px #2F886A,
		inset rem(-1) 0px 0px #2F886A;
	}

}

.button_secondary {
	border-radius: rem(25);
	height: rem(56);
	padding: rem(18) rem(26);
	background-color: var(--c-dark);

	font-weight: 500;
	color: white;
	font-size: rem(14);

	transition: all 200ms ease;

	&:hover {
		background-color: #30384B;
	}

	&:active {
		box-shadow: inset 0px rem(1) 0px var(--c-dark),
			inset 0px rem(-1)0px var(--c-dark),
			inset rem(1) 0px 0px var(--c-dark),
			inset rem(-1) 0px 0px var(--c-dark);
	}

}


// styles merriweather

h1 {
	font-size: rem(24);
	line-height: 150%;
	font-family: $font-merri;
	color: var(--c-dark);
}

h3,
.inside_title {
	font-size: rem(18);
	line-height: 164%;
	font-weight: normal;
	font-family: $font-merri;
	color: var(--c-dark);
	letter-spacing: rem(-0.2);
}

p.text-merri {
	font-size: rem(15);
	line-height: rem(28);
	font-family: $font-merri;
	color: var(--c-dark);
	letter-spacing: rem(-0.2);
}

// styles mplus

h2 {
	font-size: rem(22);
	line-height: 148%;
	letter-spacing: rem(0.2);
	font-weight: 500;

	@include min340 {
		font-size: rem(20);
	}
}

