.popup_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(48, 56, 75, 0.86);
	backdrop-filter: blur(4px);

	z-index: 100;
	padding: rem(64) 0;

	display: none;

	&__close {
		position: absolute;
		top: 0;
		right: 0;
		width: rem(32);
		height: rem(32);

		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(28);
		}
	}

	&__wrap {
		position: relative;
		width: calc(100% - 40px);
		margin: 0 rem(20);
		border-radius: rem(5);
		background-color: white;
		height: 100%;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		h3 {
			font-weight: normal;
			margin-bottom: rem(12);
		}

		p {
			text-align: center;
			max-width: rem(250);
			margin-left: auto;
			margin-right: auto;
		}
	}
}

.popup_active {
	display: block;
}