@font-face {
	font-family: 'Mplus 1p';
	src: local('Mplus 1p Light'), local('Mplus1p-Light'),
		url('../fonts/Mplus1p/Mplus1p-Light.woff2') format('woff2'),
		url('../fonts/Mplus1p/Mplus1p-Light.woff') format('woff');
	font-weight: 300;
	font-style: normal;
}

