.feedback {
	position: relative;
	padding: rem(46) 0;

	background-color: var(--c-accent);

	&::before {
		content: "";
		position: absolute;
		top: rem(315);
		left: 0;
		background: url(../img/feedback_path1.png) no-repeat;
		background-size: 100%;

		width: rem(36);
		height: rem(72);

		// z-index: 1;
	}

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		right: rem(12);
		background: url(../img/feedback_path2.png) no-repeat;
		background-size: 100%;

		width: rem(84);
		height: rem(79);

		// z-index: 1;
	}

	>.container {
		display: flex;
		// flex-wrap: wrap;
		flex-direction: column-reverse;

		// margin-left: rem(100);
		// margin-right: rem(100);
	}

	&__left {
		h2,
		> p,
		.feedback__tel,
		.feedback__email {
			display: none;
		}
	}

	&__form {
		display: flex;
		flex-direction: column;
		border-radius: rem(5);
		overflow: hidden;
		background-color: white;

		margin-bottom: rem(12);

		&-header {
			height: rem(55);
			width: 100%;
			background-color: #F1F6FD;

			display: flex;
			align-items: center;
			justify-content: center;

			h2 {
				text-align: center;
			}
		}

		&-wrap {
			padding: rem(27) rem(28);

			.field_set {
				margin-bottom: rem(7);
			}

			textarea.field {
				height: rem(138);
				min-height: rem(138);
				max-height: rem(200);
			}

			.button_secondary {
				width: 100%;
				margin-top: rem(13);
				padding: rem(11) 0;
				height: auto;
			}
		}
	}

	&__text {
		max-width: rem(263);
		font-size: rem(12);
		font-weight: 300;
		color: white;
		margin-bottom: rem(20);

		a {
			border-bottom: rem(1) dashed white;

			&:hover {
				border-bottom: 0;
			}
		}
	}
}

.block_icon_text {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.image {
		margin-right: rem(12);
	}

	p {
		font-weight: 500;
		color: white;
	}
}