.cookie_info {
	position: fixed;
	left: rem(20);
	bottom: rem(20);
	width: calc(100% - 40px);
	background-color: #4E545A;
	border-radius: rem(4);
	padding: rem(16) rem(70) rem(16) rem(14);

	z-index: 1000;

	&__first {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		p {
			max-width: rem(180);


			@include min340 {
				max-width: rem(150);
			}
		}

		p, button {
			color: white;
			font-size: rem(14);
			font-weight: bold;
		}

		.b1 {
			height: rem(20);
			// display: block;
			// line-height: 70%;
			color: #E5E8EC;
			border-bottom: rem(1) dashed #E5E8EC;
		}

		.b2 {
			text-transform: uppercase;
			padding: rem(4) rem(10);
			background-color: #60666B;
			position: absolute;
			top: 50%;
			margin-top: rem(-14);
			right: rem(14);
			height: rem(28);
		}
	}

	&__last {
		display: none;
		p,
		a {
			color: white;
			font-size: rem(14);
			font-weight: bold;
		}

		p {
			max-width: rem(278);

			@include min340 {
				max-width: rem(250);
			}
		}

		a {
			display: inline-block;
			margin-top: rem(8);
			border-bottom: rem(1) dashed #E5E8EC;
			white-space: nowrap;
		}
	}
}

.cookie_info.cookie_info__show {
	.cookie_info__last {
		display: block;
	}
	.cookie_info__first {
		p,
		.b1 {
			display: none;
		}
		.b2 {
			top: rem(16);
			margin-top: 0;
		}
	}
}