
.sertifications_accordion {
	background-color: #F1F6FD;
	padding: rem(64) 0;

	display: flex;
	flex-wrap: wrap;

	.w-7\/12 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	&__left {
		flex: 0 0 100%;
		max-width: 100%;

		h2 {
			display: none;
		}

		.accordion {
			&__item {
				// margin-bottom: rem(28);

				&:last-child {
					// margin-bottom: 0;
				}

				.title {
					cursor: pointer;
					padding: rem(30) 0;
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					align-items: center;
					border-bottom: rem(1) solid var(--c-dark);

					span {
						font-size: rem(14);
						font-weight: 500;
						line-height: 152%;
						text-transform: uppercase;
					}

					button {
						width: rem(28);
						height: rem(28);

						display: flex;
						align-items: center;
						justify-content: center;

						&::before {
							content: "\e90c";
							font-family: 'icons';
							color: var(--c-dark);
							font-size: rem(28);
						}
					}
				}

				.text {
					margin-top: rem(10);

					display: none;

					p {
						font-size: rem(14);
						font-weight: 300;
						line-height: 191%;
						font-family: $font-merri;
					}
				}
			}

			&__item.active {

				.title {
					button {
						&::before {
							content: "\e916";
							font-size: rem(1);
						}
					}
				}

				.text {
					display: block;
				}
			}
		}
		
	}

	&__right {
		display: none;
	}
}