#header_info {
	position: relative;
	height: rem(242);
	background: url(../img/page_info/header_bg_info_x1.jpg) no-repeat;
	background-size: cover;

	overflow: hidden;

	> .container {
		position: relative;

		&::before,
		&::after {
			content: "";
			position: absolute;
			pointer-events: none;
		}

		&::before {
			bottom: 0;
			left: rem(-20);
			background: url(../img/page_info/header_i_left_x1.png) top center;
			background-size: 100%;
			width: rem(91);
			height: rem(242);
		}

		&::after {
			bottom: rem(-70);
			right: rem(-20);
			background: url(../img/page_info/header_i_right_x1.png) top center;
			background-size: 100%;
			width: rem(91);
			height: rem(242);
		}
	}

	

	h1 {
		font-size: rem(22);
		font-weight: normal;
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(44);
		padding-bottom: 0;

		
	}

