.articles {
	padding: rem(64) 0;

	> .container {
		display: flex;
		flex-direction: column;
	}

	h2 {
		margin-bottom: rem(48);
	}

	&__items {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(52);

		.item {
			margin-bottom: rem(24);

			padding-bottom: rem(24);

			border-bottom: rem(1) solid #E5E8EC;

			&:last-child {
				margin-bottom: 0;
			}

			&__top {
				margin-bottom: rem(16);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			&__bottom {

				.subtitle {
					font-size: rem(10);
					font-weight: 500;
					line-height: 132%;
					text-transform: uppercase;
					display: block;
					margin-bottom: rem(12);
					letter-spacing: 2%;
				}

				.inside_title {
					margin-bottom: rem(34);
					display: block;
					line-height: 143%;
					transition: all 300ms ease;

					&:hover {
						color: var(--c-accent);
					}
				}

				.author {
					display: flex;
					flex-wrap: wrap;
					align-items: flex-end;

					.image {
						width: rem(37);
						height: rem(37);
						border-radius: 50%;
						overflow: hidden;
						margin-right: rem(8);
					}

					.text {

						span {
							display: block;
							font-size: rem(10);
							letter-spacing: 2%;

							&:first-child {
								font-weight: 500;
								text-transform: uppercase;
								margin-bottom: rem(4);
							}

					
						}
					}
				}
			}
		}
	}

	.button_primary {
		align-self: center;
		height: auto;
		padding: rem(11) rem(23);
	}
}