.product_article {
	padding: rem(64) 0;

	h2 {
		margin-bottom: rem(48);
		text-align: center;
	}

	&__items {
		display: flex;
		flex-direction: column;
		padding-bottom: rem(34);
		margin-bottom: rem(34);
		border-bottom: rem(1) solid #ADADAD;

		.item {
			margin-bottom: rem(36);

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-bottom: rem(20);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			h3 {
				margin-bottom: rem(12);
				font-weight: normal;
				line-height: 143%;

				letter-spacing: (0.12rem);
			}

			p {
				margin-bottom: rem(20);

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}
	}

	&__text {
		display: flex;
		// flex-wrap: wrap;

		.image {
			margin-right: rem(12);
		}

		h3 {
			font-weight: normal;
		}
	}
}