.review {
	padding: rem(64) 0;
	background-color: var(--c-accent);

	&__top {
		margin-bottom: rem(36);
		text-align: center;

		h2 {
			margin-bottom: rem(8);
		}

		p {
			padding: 0 rem(10);
		}

		h2, p {
			color: white;
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		align-items: center;

		.swiper-container {
			width: 100%;
			
		}

		.swiper-wrapper {
			margin-bottom: rem(28);
			width: 100%;
			
		}

		.swiper-nav {
			margin-bottom: rem(42);
		}

		.button_secondary {
			height: auto;
			padding: rem(11) rem(40);
		}

		.swiper-slide {

			&:nth-child(1n+3) {
				.review__item {
					&-bottom {
						.image {
							background-color: #C2E2D7;
						}
					}
				}
			}

			&:nth-child(2n+3) {
				.review__item {
					&-bottom {
						.image {
							background-color: #FFEFE1;
						}
					}
				}
			}
			
			
		}
	}

	&__item {
		background-color: #FFFFFF;
		border-radius: rem(3);
		padding: rem(18) rem(12);
		width: 100%;
		height: 100%;
		
		display: flex;
		flex-direction: column;
		transition: all 150ms ease;

		&-top {
			margin-bottom: rem(8);

			p {
				margin-bottom: rem(8);
			}

			span {
				font-size: rem(10);
				font-family: $font-roboto;
				text-transform: uppercase;
				color: #ADADAD;
			}
		}

		

		&-bottom {

			margin-top: auto;
			
			.image {
				width: rem(82);
				height: rem(82);
				border-radius: 50%;

				display: flex;
				align-items: center;
				justify-content: center;

				padding: rem(5);
				background-color: #C2E2D7;
			}
		}
	}
}

.user_info {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&__image {
		margin-right: rem(8);
		width: rem(48);
		height: rem(48);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__text {
		.title {
			display: block;
			margin-bottom: rem(2);
			font-size: rem(12);
			font-weight: 500;
		}

		.icons {
			display: flex;
			flex-wrap: wrap;

			i {
				margin-right: rem(3.5);

				&:last-child {
					margin-right: 0;
				}
			}

			.star-gray {
				color: #B4BCC4;

				&::before {
					color: #B4BCC4;
				}
			}
		}
	}
}

.block_icon {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&__image {
		width: rem(14);
		height: rem(14);
		border-radius: 50%;
		background-color: var(--c-accent);

		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(4);

		i {
			font-size: rem(6);
		}
	}

	p {
		color: #4E545A;
	}
}

.swiper-nav {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;

	&-prev, &-next {
		i {
			font-size: rem(20);
			transition: all 300ms ease;
		}
	}

	&-prev {
		margin-right: rem(20);
	}

	// &-next {
	// 	i {
	// 		font-size: rem(20);
	// 	}
	// }

	.swiper-button-disabled {
		cursor: default;
		i {
			font-size: rem(12);
			opacity: .3;
		}
	}
}