.benefits {
	padding-bottom: rem(64);
	&__top {
		margin-bottom: rem(48);
		h2 {
			text-align: center;
		}
	}

	&__bottom {

		&-items {
			margin-bottom: rem(40);

			.item {
				text-align: center;
				margin-bottom: rem(36);

				&:last-child {
					margin-bottom: 0;
				}

				&__image {
					margin-bottom: rem(24);
				}

				&__info {

					h3 {
						margin-bottom: rem(10);
						font-weight: normal;
					}
				}
			}
		}

		.button_secondary {
			width: 100%;
			display: flex;
			justify-content: center;
		}
	}
}