#header_goods_about {
	height: auto;
	background: none;
	background-size: cover;

	>.container {
		width: 100%;
	}

	.header__top {
		margin-left: 20px;
		margin-right: 20px;
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(10);
		padding-bottom: 0;

		display: flex;
		flex-wrap: wrap;

		&-left,
		&-right {
			flex: 0 0 100%;
			max-width: 100%;
		}

		&-left {
			position: relative;
			width: 100%;
			height: rem(298);
			background: url(../img/page_goods_about/header_bg_x1.jpg) no-repeat top center;
			background-size: cover;

			display: flex;
			align-items: center;
			justify-content: center;

			padding-bottom: rem(40);
			padding-top: rem(8);

			.swiper-overlay {
				width: rem(250);
				height: rem(250);
				border-radius: 50%;
				background-color: #F1F6FD;

				display: flex;
				align-items: center;
				justify-content: center;

				.swiper-container {
					padding-bottom: rem(40);
					margin-top: rem(60);
					// height: rem(250);
				}
			}

			.swiper-nav {
				position: absolute;
				bottom: rem(10);
				left: 50%;
				margin-left: rem(-34);

				button {
					i {
						&::before {
							color: #30384B;
						}
					}
				}
			}
			.swiper-nav.hidden {
				opacity: 0;
			}
		}

		&-right {
			// margin-top: rem(20);
			margin: rem(20);

			display: flex;
			flex-direction: column;

			> span {
				display: block;
				font-size: rem(11);
				font-weight: 500;
				line-height: 144%;
				text-transform: uppercase;
				padding-bottom: rem(16);
				border-bottom: rem(1) solid var(--c-dark);

				margin-bottom: rem(32);
			}

			h3 {
				font-size: rem(28);
				line-height: 150%;
			}

			p {
				font-family: $font-merri;
				line-height: 163%;
				font-size: rem(18);
				color: #30384B;
				text-align: left;
				margin-bottom: rem(6);
				// letter-spacing: rem(0.2);
			}

			.icons {
				margin-right: rem(12);
				i {
					font-size: rem(18);
				}
			}

			.count {
				font-size: rem(9);
				line-height: rem(14);
				font-weight: 500;
				border-bottom: rem(1) solid var(--c-dark);
			}

			a.button_primary {
				order: 5;
				margin-top: rem(28);
				margin-bottom: rem(40);
				text-align: center;
			}

			ul {
				order: 6;
				margin-bottom: rem(40);

				li {
					position: relative;
					margin-bottom: rem(12);
					font-size: rem(12);
					font-weight: 500;
					line-height: 130%;
					padding-left: rem(20);

					&::before {
						content: "";
						position: absolute;
						top: rem(4);
						left: 0;
						width: rem(6);
						height: rem(6);
						border-radius: 50%;
						background-color: var(--c-dark);
					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			a {
				order: 7;
				font-size: rem(12);
				font-weight: 500;
				line-height: 130%;
				align-self: flex-start;
				border-bottom: rem(1.5) solid var(--c-dark);
			}
		}
	}
}

#header_goods_about.header_fixed {
	>.container {
		max-width: 1400px;
		width: calc(100% - 40px);
	}

	.header__top {
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		padding: 1.25rem;
		z-index: 10;
		background-color: #D5E4F3;

		margin-left: auto;
		margin-right: auto;

		&::before,
		&::after {
			display: none;
		}
	}
}