.slider_product {

	

	.swiper-container {
		padding: rem(36) rem(30);
	}

	.swiper-wrapper {
		// z-index: 6;
	}

	.swiper-nav {
		// position: absolute;
		// top: 0;
		// left: 0;
		// width: 100%;
		// height: 100%;
		
		justify-content: space-between;

		button {
			z-index: 5;
			position: absolute;
			top: 50%;
			margin-top: rem(-40);
			margin-bottom: rem(40);
		}

		.swiper-nav-prev {
			left: 0;
		}

		.swiper-nav-next {
			right: 0;
		}

		.swiper-button-disabled {
			i {
				font-size: rem(12);
				&::before {
					font-size: rem(12);
				}
			}
		}

		i {
			

			&::before {
				color: var(--c-accent);
				font-size: rem(12);
			}
		}
	}

	.swiper-slide {
		&:first-child {
			.slider_product__item {
				.image {
					background-color: #FFE1E1;
				}
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;

		.image {
			position: relative;
			width: rem(104);
			height: rem(104);
			border-radius: 50%;

			display: flex;
			align-items: center;
			justify-content: center;

			margin-bottom: rem(25);
			background-color: #FFF2D4;

			img {
				position: absolute;
				top: rem(-12);

				width: rem(112);
				height: rem(128);
				// height: 100%;
				object-fit: cover;
			}
		}

		h3 {
			font-size: rem(10);
			text-align: center;
			max-width: rem(112);
			font-weight: normal;
		}
	}

}