.etaps {
	background-color: #F1F6FD;
	padding: rem(64) 0;

	&__items {
		display: flex;
		flex-direction: column;

		.item {
			background-color: white;
			border-radius: rem(5);
			padding: rem(36) rem(24) rem(15) rem(24);
			box-shadow: 0px 2px 4px rgba(105, 183, 220, 0.07);

			margin-bottom: rem(28);

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-bottom: rem(28);
			}

			h3 {
				margin-bottom: rem(4);
			}

			p {
				margin-bottom: rem(20);

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}
	}
}