.partners {
	padding: rem(64) 0;
	background-color: #F1F6FD;

	h2 {
		text-align: center;
		margin-bottom: rem(48);
	}

	&__items {
		padding: 0 rem(43);

		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		.item {
			display: flex;
			flex: 0 0 50%;
			max-width: 50%;
			margin-bottom: rem(24);

			&:nth-child(2n) {
				justify-content: flex-end;
			}
			&:last-child {
				justify-content: center;
				margin-bottom: 0;
			}

			a {
				display: block;

				img {
					transition: all 100ms ease;
					filter: grayscale(100%);
				}

				&:hover {
					img {
						filter: grayscale(0%);
					}
				}
			}
		}
	}
}