.footer {
	background-color: #30384B;
	padding-top: rem(32);

	hr {
		width: 100%;
		height: rem(1);
		background-color: rgba(229, 232, 236, .3);
	}

	&__top {
		padding-bottom: rem(48);

		&-nav {
			display: flex;
			flex-wrap: wrap;

			.items {
				display: flex;
				flex-wrap: wrap;
				width: 100%;
			}

			.item {
				margin-bottom: rem(28);

				flex: 0 0 46%;
				max-width: 46%;

				&:first-child,
				&:nth-child(3) {
					margin-right: rem(10);
				}

				&:nth-child(3), &:last-child {
					margin-bottom: 0;
				}

				@include min360 {
					&:nth-child(3) {
						margin-bottom: rem(28);
					}
				}

				.title {
					font-size: rem(11);
					font-weight: 500;
					color: white;
					padding-bottom: rem(8);
					margin-bottom: rem(12);

					white-space: nowrap;

					display: inline-block;
					text-transform: uppercase;

					border-bottom: rem(2) solid var(--c-accent);
				}

				ul {
					
					li {

						margin-bottom: rem(6);

						&:last-child {
							margin-bottom: 0;
						}

						a {
							font-size: rem(13);
							color: white;
						}
					}
				}
			}
		}


		&-left {
			.logo {
				max-width: rem(150);
				margin-bottom: rem(12);
			}
			.links {
				margin-bottom: rem(16);

				a {
					color: white;
					display: block;

					&:first-child {
						font-size: rem(15);
						font-weight: 500;
						margin-bottom: rem(2);
					}

					&:last-child {
						font-size: rem(12);
					}
				}
			}

			.copy {
				margin-bottom: rem(36);

				span {
					color: white;
					font-size: rem(14);
					text-transform: uppercase;
				}
			}
		}

		&-right {
			display: none;
		}
	}

	&__bottom {
		padding: rem(16) 0;

		.links {
			display: flex;
			flex-wrap: wrap;

			a {
				position: relative;
				color: white;
				line-height: 146%;
				font-size: rem(11);
				white-space: nowrap;

				&:first-child {
					margin-right: rem(30);

					&::after {
						content: "";
						position: absolute;
						top: rem(2);
						right: rem(-15);
						height: rem(12);
						width: rem(1);
						background-color: white;

						@include min360 {
							display: none;
						}
					}
				}
			}
		}
	}
}