.catalog {
	padding: rem(20) 0 rem(64) 0;

	&__header {
		span {
			font-size: rem(11);
			font-weight: 500;
			text-transform: uppercase;
			text-align: center;
			letter-spacing: rem(1.8);
			display: block;
			margin-bottom: rem(20);
		}

		&-links {
			display: flex;
			flex-wrap: wrap;
			flex-direction: row-reverse;

			border-bottom: rem(1) solid #E5E8EC;
			border-top: rem(1) solid #E5E8EC;

			// padding: 0 rem(20);
			margin-left: rem(-20);
			margin-right: rem(-20);

			margin-bottom: rem(20);

			.item {
				height: rem(50);
				flex: 0 0 50%;
				max-width: 50%;

				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;

				&:last-child {
					border-right: rem(1) solid #E5E8EC;
				}

				a {
					font-weight: bold;
					margin-left: rem(8);
					pointer-events: none;
				}

				button {
					width: rem(28);
					height: rem(28);

					display: flex;
					align-items: center;
					justify-content: center;

					i {
						font-size: rem(32);
					}
				}
			}
		}
	}

	&__info {
		flex: 0 0 100%;
		max-width: 100%;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		margin-bottom: rem(28);

		.count {
			span {
				font-size: rem(11);
				font-weight: 800;
				&:first-child {
					margin-right: rem(8);
				}
				&:last-child {
					color: var(--c-accent);
				}
			}
		}
	}

	&__left {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: white;
		z-index: 150;
		overflow-y: auto;

		display: none;

		&-header {
			position: relative;
			height: rem(56);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: center;
			border-top: rem(1) solid #E5E8EC;
			border-bottom: rem(1) solid #E5E8EC;

			span {
				font-size: rem(11);
				line-height: 144%;
				letter-spacing: rem(1.5);
				font-weight: 500;
				text-transform: uppercase;
			}

			.catalog_close {
				position: absolute;
				top: rem(11);
				right: rem(19);
				width: rem(32);
				height: rem(32);
				display: flex;
				align-items: center;
				justify-content: center;

				i {
					font-size: rem(28);
				}
			}
		}

		&-nav {
			padding: rem(60) rem(50) rem(20) rem(50);

			ul {
				border-bottom: rem(1) solid #E5E8EC;

				display: flex;
				flex-direction: column;

				margin-bottom: rem(10);

				li {
					// margin-bottom: rem(20);
					&:last-child {
						margin-bottom: rem(10);
					}
					a {
						display: block;
						font-weight: 500;
						line-height: 237%;
						padding: rem(10) 0;

						&:hover {
							color: var(--c-accent);
						}
					}
				}

				li.active {
					a {
						color: var(--c-accent);
					}
				}
			}
		}
	}

	&__left#catalog_mod_1.active {
		display: block;
	}
	&__left#catalog_mod_2.active {
		display: block;
	}
	
	&__right {
		flex: 0 0 100%;
		max-width: 100%;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		// margin-left: rem(-10);
		// margin-right: rem(-10);

		.product_item {
			width: calc((100% - 0.8rem) / 2);

			&:nth-child(4n+3),
			&:nth-child(4n+4) {
				.product_item__image {
					&::before {
						background-color: #FFF2D4;
					}
				}
			}
		}
	}
}

.product_item {
	position: relative;
	border-radius: rem(4);
	border: rem(1) solid #F4F1ED;

	padding: rem(6);

	display: flex;
	flex-direction: column;

	margin-bottom: rem(14);

	overflow: hidden;

	&__subcat {
		position: absolute;
		top: 0;
		right: 0;
		font-weight: bold;
		font-size: rem(12);
		text-transform: uppercase;
		color: white;
		padding: rem(7) rem(6);
		background-color: var(--c-accent);
		border-radius: rem(1);
	}

	&__image {
		position: relative;
		display: flex;
		justify-content: center;
		margin-bottom: rem(12);

		&::before {
			content: "";
			position: absolute;
			width: rem(150);
			height: rem(150);
			border-radius: 50%;
			background-color: #E0E9F4;
			top: rem(20);
			left: 50%;
			margin-left: rem(-75);
			z-index: -1;
		}

		img {
			width: rem(184);
		}
	}

	&__text {
		h3, p {
			text-align: center;
		}

		h3 {
			margin-bottom: rem(6);
			font-size: rem(14);
			font-weight: 300;
		}

		p {
			font-size: rem(11);
		}
	}
}


.page_nav {
	margin-top: rem(36);
	display: flex;
	justify-content: center;
	align-items: center;

	ul {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		li {
			margin-right: rem(20);

			&:last-child {
				margin-right: 0;
			}

			span, a {
				font-weight: 800;
				line-height: 144%;
				color: var(--c-accent);
				font-size: rem(11);
			}

			a {
				display: block;
				&:hover {
					color: var(--c-dark);
				}
			}

			.arrow {
				display: flex;
				align-items: center;
				i {
					color: var(--c-accent);
					font-size: rem(18);
					font-weight: 800;
				}
			}
		}

		li.active {
			a {
				color: var(--c-dark);
			}
		}
	}
}