.subscribe {
	padding: rem(48) 0;
	background-color: var(--c-accent);

	> .container {
		padding-left: rem(27);
		padding-right: rem(27);
	}

	h2 {
		margin-bottom: rem(20);
		color: white;
		text-align: center;
	}

	&__form {
		position: relative;
		display: flex;
		flex-direction: column;
		margin-bottom: rem(20);
		

		.field {
			margin-bottom: rem(16);
			border-color: var(--c-dark);

			

			
		}

		.form_success {
			position: absolute;
			top: 0;
			left: 0;

			// display: flex;
			// flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			width: 100%;

			display: none;

			p {
				font-size: rem(14);
				color: white;
				font-weight: 500;
			}

			button {
				width: rem(32);
				height: rem(32);

				display: flex;
				align-items: center;
				justify-content: center;
				

				i {
					color: white;
					font-size: rem(24);

					&::before {
						color: white;
					}
				}
			}
		}

		.button_secondary {
			height: auto;
			padding: rem(11) rem(15);
		}
	}

	.form_send_success {
		.field_set {
			opacity: 0;
		}

		.form_success {
			display: flex;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		.item {
			flex: 0 0 calc((100% - 2.5rem) / 2);
			// max-width: 50%;

			display: flex;
			// flex-wrap: wrap;
			align-items: center;

			margin-right: rem(20);
			margin-bottom: rem(2);

			&:nth-child(2), &:last-child {
				margin-right: 0;
			}

			i {
				margin-right: rem(4);
			}

			span {
				color: white;
				white-space: nowrap;
			}
		}
	}
}