.numbers {
	// margin-top: rem(24);
	margin-bottom: rem(60);

	&__top {
		position: relative;

		img {
			width: 100%;
		}
	}

	&__bottom {
		position: relative;
		z-index: -1;
		top: rem(-32);
		background: url(../img/section_numbers_bg_x1.jpg) no-repeat top center;
		background-size: cover;

		padding-top: rem(93);
		padding-bottom: rem(64);

		&::before {
			content: "";
			position: absolute;
			top: rem(-119);
			left: 0;
			width: 100%;
			height: rem(120);
			background-color: #157C69;
		}

		h2 {
			// font-size: rem(24);
			// line-height: 144%;
			// letter-spacing: 1%;
			color: white;
			// font-weight: 500;

			margin-bottom: rem(8);

			padding-right: rem(6);
		}

		p.text {
			color: white;
			margin-bottom: rem(40);
		}

		&-items {
			display: flex;
			flex-direction: column;

			.item {
				padding-left: rem(14);
				margin-bottom: rem(24);

				&:last-child {
					margin-bottom: 0;
				}

				&__title {
					position: relative;
					font-size: rem(20);
					font-weight: 500;
					color: white;
					margin-bottom: rem(4);

					&::before {
						content: "";
						position: absolute;
						top: rem(6);
						left: rem(-14);
						width: rem(2.5);
						height: rem(24);
						background-color: white;
					}
				}

				&__text {
					color: white;
				}
			}
		}
	}
}