.our_values {
	background-color: #F1F6FD;
	padding: rem(84) 0 rem(30) 0;

	h2 {
		margin-bottom: rem(48);
	}


	&__items {
		display: flex;
		flex-direction: column;

		.item {
			background-color: white;
			border-radius: rem(4);
			padding: rem(16);
			margin-bottom: rem(24);

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-bottom: rem(20);
			}

			h3 {
				margin-bottom: rem(8);
			}

			p {
				color: #4E545A;
			}
		}
	}
}