.products {
	padding: rem(64) 0;

	> .container {
		display: flex;
		flex-direction: column;
		align-items: center;

		h2 {
			text-align: center;
			margin-bottom: rem(48);
		}
	}

	&__items {
		margin-bottom: rem(40);

		.item {
			
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: rem(36);

			&:last-child {
				margin-bottom: 0;
			}

			&:first-child,
			&:nth-child(2),
			&:nth-child(3),
			&:last-child {
				.item__image {
					&::before {
						content: "";
						position: absolute;
						top: rem(-10);
						left: rem(-10);
						width: rem(220);
						height: rem(220);
						border-radius: 50%;
						
						z-index: -1;
					}
				}
			}

			&:first-child {
				.item__image {
					&::before {
						background-color: #E0E9F4;
					}
				}
			}

			&:nth-child(2) {
				.item__image {
					&::before {
						background-color: #E8F2EB;
					}
				}
			}

			&:nth-child(3) {
				.item__image {
					&::before {
						background-color: #FFF2D4;
					}
				}
			}

			&:last-child {
				.item__image {
					&::before {
						background-color: #FFE1E1;
					}
				}
			}

			&__image {
				position: relative;
				margin-bottom: rem(20);
			}
			&__info {
				text-align: center;

				h3 {
					font-weight: normal;
					margin-bottom: rem(4);

					br {
						display: none;
					}
				}
			}
		}
	}

	.button_primary {
		height: auto;
		padding: rem(11) rem(40);
	}
}