.team {
	padding: rem(84) 0;
	background-color: var(--c-accent);

	>.container {
		display: flex;
		justify-content: center;
		flex-direction: column;
	}

	h2 {
		margin-bottom: rem(12);
		text-align: center;
		max-width: rem(272);
		margin-left: auto;
		color: white;
		margin-right: auto;
	}

	p {
		font-size: rem(15);
		text-align: center;
		color: white;

		a {
			font-weight: bold;
			text-decoration: underline;
			display: block;
		}
	}
}